<template>
    <section>
        <div>This is the search result page. Your search is loading......</div>
        <!--there should be a search result item that displays the search results on load using v-for.-->
    </section>    
</template>

<script>
export default {
    methods: {
        search(MID,DID,searchInput){
            let url = "http://localhost/search/"
            //let url = "/search/"
            window.axios.get(url,{
                params : {
                    MID , DID , searchInput
                }
            }).then((response)=>{
                console.log( "search result",response.data);
            }).catch((error)=>{
                console.error("An error occured : ", error.message);
            })
        }
    },
    created : function () {
        let MID = window.localStorage.getItem("userlogindata").MID,DID = window.localStorage.getItem("userlogindata").DID;
        let searchInput = this.$store.state.searchItem;
        console.log(MID,DID,searchInput);
        this.search(MID,DID,searchInput);
    }
}
</script>

